.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8 rem;
    position: absolute;
    left: 0;
    top: 30rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me{
    background:linear-gradient(var(--color-primary),transparent) ;
    width: 22rem;
    height: 30rem;
    position:absolute;
    left: calc(50% - 11rem);
    margin-top: 5rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.scroll__down{
    position: absolute;
    right: -2.3rem;
    
    transform:rotate(90deg) ;
    font-weight: 300;
    font-size: 0.9rem;
    top: 32rem;
}

/* =============== MEDIA QUERIES (MEDIUM DEVICES) =========== */

@media screen and (max-width:1024px) {
    
    .header{
        height: 68vh;
    }
    
}

/* =============== MEDIA QUERIES (SMALL DEVICES) =========== */

@media screen and (max-width:600px) {
    .header{
        height: 100vh;
    }
    .header__socials,
    .scroll__down{
        display: none;
    }
    
}
